<template>
    <div v-if="lan=='TW'">
        <h2> API Base</h2>
        <h4 id="BaseComponent"></h4>
        <div>
            <b-button v-clipboard:copy="copyBaseComponent" @click="Alert('Copy: ' + copyBaseComponent)">Copy Import</b-button>
            <h5>: BaseComponent</h5>
            <h5></h5>
            <p>
                 Xanthus , 
            </p>
            <h5></h5>
            <b-table striped hover :items="baseComponentFunction"/>
        </div>
        <h4 id="Search"></h4>
        <div>
            <b-button v-clipboard:copy="copySearchBar" @click="Alert('Copy: ' + copySearchBar)">Copy Import</b-button>
            <h5>: SearchBar</h5>
            <h5>:</h5>
            <b-table striped hover :items="searchBarProp"/>
            <h5>:</h5>
            <b-table striped hover :items="searchBarEvent"/>
        </div>
        <h4 id="UserAvatar"></h4>
        <div>
            <b-button v-clipboard:copy="copyUserAvatar" @click="Alert('Copy: ' + copyUserAvatar)">Copy Import</b-button>
            <h5>: UserAvatar</h5>
            <h5>:</h5>
            <b-table striped hover :items="userAvatarProp"/>
            <h5>:</h5>
            <b-table striped hover :items="userAvatarEvent"/>
        </div>
        <h4 id="TopButton"></h4>
        <div>
            <b-button v-clipboard:copy="copyTopButton" @click="Alert('Copy: ' + copyTopButton)">Copy Import</b-button>
            <h5>: TopButton</h5>
            <h5>:</h5>
            <b-table striped hover :items="topButtonProp"/>
            <h5>:</h5>
            <b-table striped hover :items="topButtonEvent"/>
        </div>
        <h4 id="TopFilter"></h4>
        <div>
            <b-button v-clipboard:copy="copyTopFilter" @click="Alert('Copy: ' + copyTopFilter)">Copy Import</b-button>
            <h5>: TopFilter</h5>
            <h5>:</h5>
            <b-table striped hover :items="topFilterProp"/>
            <h5>:</h5>
            <b-table striped hover :items="topFilterEvent"/>
        </div>
        <h4 id="TopDropdown"></h4>
        <div>
            <b-button v-clipboard:copy="copyTopDropdown" @click="Alert('Copy: ' + copyTopDropdown)">Copy Import</b-button>
            <h5>: TopDropdown</h5>
            <h5>:</h5>
            <b-table striped hover :items="topDropdownProp"/>
            <h5>:</h5>
            <b-table striped hover :items="dropdownEvent"/>
        </div>
        <h4 id="PageSelection"></h4>
        <div>
            <b-button v-clipboard:copy="copyPageSelector" @click="Alert('Copy: ' + copyPageSelector)">Copy Import</b-button>
            <h5>: PageSelector</h5>
            <h5>:</h5>
            <b-table striped hover :items="pageSelectorProp"/>
            <h5>:</h5>
            <b-table striped hover :items="pageSelectorEvent"/>
        </div>
        <h4 id="DialogTemp"></h4>
        <div>
            <b-button v-clipboard:copy="copyDialogTemp" @click="Alert('Copy: ' + copyDialogTemp)">Copy Import</b-button>
            <h5>: DialogTemp</h5>
            <h5>:</h5>
            <b-table striped hover :items="dialogTempProp"/>
            <h5>:</h5>
            <b-table striped hover :items="dialogTempEvent"/>
        </div>
        <h4 id="Date-Picker"></h4>
        <div>
            <b-button v-clipboard:copy="copyDatePicker" @click="Alert('Copy: ' + copyDatePicker)">Copy Import</b-button>
            <h5>: DatePicker</h5>
            <h5>:</h5>
            <b-table striped hover :items="datePickerProp"/>
            <h5>:</h5>
            <b-table striped hover :items="datePickerEvent"/>
        </div>
        <h4 id="Time-Picker"></h4>
        <div>
            <b-button v-clipboard:copy="copyTimePicker" @click="Alert('Copy: ' + copyTimePicker)">Copy Import</b-button>
            <h5>: TimePicker</h5>
            <h5>:</h5>
            <b-table striped hover :items="timePickerProp"/>
            <h5>:</h5>
            <b-table striped hover :items="timePickerEvent"/>
        </div>
        <h4 id="TimeSpan-Picker"></h4>
        <div>
            <b-button v-clipboard:copy="copyTimeSpanPicker" @click="Alert('Copy: ' + copyTimeSpanPicker)">Copy Import</b-button>
            <h5>: TimeSpanPicker</h5>
            <h5>:</h5>
            <b-table striped hover :items="timeSpanPickerProp"/>
            <h5>:</h5>
            <b-table striped hover :items="timeSpanPickerEvent"/>
        </div>
        <h4 id="Role-Picker"></h4>
        <h4 id="Staff-Picker"></h4>
    </div>
    <div v-else>
        <h2>Client API Base</h2>
        <h4 id="BaseComponent">BaseComponent</h4>
        <div>
            <b-button v-clipboard:copy="copyBaseComponent" @click="Alert('Copy: ' + copyBaseComponent)">Copy Import</b-button>
            <h5>Name: BaseComponent</h5>
            <p>Description</p>
            <p>
                Apply all Xanthus component, Have some utility function
            </p>
            <p>Function</p>
            <b-table striped hover :items="baseComponentFunction"/>
        </div>
        <h4 id="Search">Search Bar</h4>
        <div>
            <b-button v-clipboard:copy="copySearchBar" @click="Alert('Copy: ' + copySearchBar)">Copy Import</b-button>
            <h5>Name: SearchBar</h5>
            <h5>Prop:</h5>
            <b-table striped hover :items="searchBarProp"/>
            <h5>Event:</h5>
            <b-table striped hover :items="searchBarEvent"/>
        </div>
        <h4 id="UserAvatar">User Avatar</h4>
        <div>
            <b-button v-clipboard:copy="copyUserAvatar" @click="Alert('Copy: ' + copyUserAvatar)">Copy Import</b-button>
            <h5>Name: UserAvatar</h5>
            <h5>Prop:</h5>
            <b-table striped hover :items="userAvatarProp"/>
            <h5>Event:</h5>
            <b-table striped hover :items="userAvatarEvent"/>
        </div>
        <h4 id="TopButton">Button</h4>
        <div>
            <b-button v-clipboard:copy="copyTopButton" @click="Alert('Copy: ' + copyTopButton)">Copy Import</b-button>
            <h5>Name: TopButton</h5>
            <h5>Prop:</h5>
            <b-table striped hover :items="topButtonProp"/>
            <h5>Event:</h5>
            <b-table striped hover :items="topButtonEvent"/>
        </div>
        <h4 id="TopFilter">Filter</h4>
        <div>
            <b-button v-clipboard:copy="copyTopFilter" @click="Alert('Copy: ' + copyTopFilter)">Copy Import</b-button>
            <h5>Name: TopFilter</h5>
            <h5>Prop:</h5>
            <b-table striped hover :items="topFilterProp"/>
            <h5>Event:</h5>
            <b-table striped hover :items="topFilterEvent"/>
        </div>
        <h4 id="TopDropdown">Dropdown</h4>
        <div>
            <b-button v-clipboard:copy="copyTopDropdown" @click="Alert('Copy: ' + copyTopDropdown)">Copy Import</b-button>
            <h5>Name: TopDropdown</h5>
            <h5>Prop:</h5>
            <b-table striped hover :items="topDropdownProp"/>
            <h5>Event:</h5>
            <b-table striped hover :items="opDropdownEvent"/>
        </div>
        <h4 id="PageSelection">PageSelection</h4>
        <div>
            <b-button v-clipboard:copy="copyPageSelector" @click="Alert('Copy: ' + copyPageSelector)">Copy Import</b-button>
            <h5>Name: PageSelector</h5>
            <h5>Prop:</h5>
            <b-table striped hover :items="pageSelectorProp"/>
            <h5>Event:</h5>
            <b-table striped hover :items="pageSelectorEvent"/>
        </div>
        <h4 id="DialogTemp">DialogTemp</h4>
        <div>
            <b-button v-clipboard:copy="copyDialogTemp" @click="Alert('Copy: ' + copyDialogTemp)">Copy Import</b-button>
            <h5>Name: DialogTemp</h5>
            <h5>Prop:</h5>
            <b-table striped hover :items="dialogTempProp"/>
            <h5>Event:</h5>
            <b-table striped hover :items="dialogTempEvent"/>
        </div>
        <h4 id="Date-Picker">Date-Picker</h4>
        <div>
            <b-button v-clipboard:copy="copyDatePicker" @click="Alert('Copy: ' + copyDatePicker)">Copy Import</b-button>
            <h5>Name: DatePicker</h5>
            <h5>Prop:</h5>
            <b-table striped hover :items="datePickerProp"/>
            <h5>Event:</h5>
            <b-table striped hover :items="datePickerEvent"/>
        </div>
        <h4 id="Time-Picker">Time-Picker</h4>
        <div>
            <b-button v-clipboard:copy="copyTimePicker" @click="Alert('Copy: ' + copyTimePicker)">Copy Import</b-button>
            <h5>Name: TimePicker</h5>
            <h5>Prop:</h5>
            <b-table striped hover :items="timePickerProp"/>
            <h5>Event:</h5>
            <b-table striped hover :items="timePickerEvent"/>
        </div>
        <h4 id="TimeSpan-Picker">TimeSpan-Picker</h4>
        <div>
            <b-button v-clipboard:copy="copyTimeSpanPicker" @click="Alert('Copy: ' + copyTimeSpanPicker)">Copy Import</b-button>
            <h5>Name: TimeSpanPicker</h5>
            <h5>Prop:</h5>
            <b-table striped hover :items="timeSpanPickerProp"/>
            <h5>Event:</h5>
            <b-table striped hover :items="timeSpanPickerEvent"/>
        </div>
        <h4 id="Role-Picker">Role-Picker</h4>
        <h4 id="Staff-Picker">Staff-Picker</h4>
    </div>
</template>

<script>
import Base from '../../../Base'
import main from '../../../../main'
export default Base.extend({
    name: "ClientAPIBase",
    props:{
        lan: String
    },
    computed:{
        main(){
            return main;
        },
        copyBaseComponent(){
            return `import ComponentBase from "@/ComponentBase"`;
        },
        copySearchBar(){
            return `import Searchbar from "@/components/general/SearchBar/SearchBar.vue"`;
        },
        copyUserAvatar(){
            return `import UserAvatar from "@/components/general/UserAvatar/UserAvatar.vue"`;
        },
        copyTopButton(){
            return `import TopButton from "@/components/general/TopButton.vue"`;
        },
        copyTopFilter(){
            return `import TopFilter from "@/components/general/TopFilter.vue"`;
        },
        copyTopDropdown(){
            return `import TopDropdown from "@/components/general/TopDropdown.vue"`;
        },
        copyPageSelector(){
            return `import PageSelector from "@/components/general/PageSelector.vue"`;
        },
        copyDialogTemp(){
            return `import DialogTemp from "@/components/general/Dialog.vue"`;
        },
        copyDatePicker(){
            return `import DatePicker from "@/components/general/dialog/selector/DatePicker/DatePicker.vue"`;
        },
        copyTimePicker(){
            return `import TimePicker from "@/components/general/dialog/selector/TimePicker/TimePicker.vue"`;
        },
        copyTiemSpanPicker(){
            return `import TimeSpanPicker from "@/components/general/dialog/selector/TimeSpanPicker/TimeSpanPicker.vue"`;
        },

        baseComponentFunction(){
            if(this.lan === "TW"){
                return [
                    { : "GetLanguageTags", : "" },
                    { : "GetLanguageList", : ", " },
                    { : "GetLanguage", : ",  Key" },
                    { : "GetDate", : " => YYYY-MM-DD" },
                    { : "GetPreciseDate", : " => YYYY-MM-DD:hh:mm:ss" },
                    { : "GetTime", : " => dd:hh:mm:ss" },
                    { : "GetShortTime", : " => hh:mm" },
                    { : "TimeGapSmall", : ", , , " },
                    { : "TimeGapBig", : ", , , " },
                    { : "OpenURL", : "" },
                ]
            }else{
                return [
                    { Name: "GetLanguageTags", Description: "Get language tags from server" },
                    { Name: "GetLanguageList", Description: "Get language data from server according to input tag" },
                    { Name: "GetLanguage", Description: "Search language value by key" },
                    { Name: "GetDate", Description: "Date time to string => YYYY-MM-DD" },
                    { Name: "GetPreciseDate", Description: "Date time to string => YYYY-MM-DD:hh:mm:ss" },
                    { Name: "GetTime", Description: "Time duration to string => dd:hh:mm:ss" },
                    { Name: "GetShortTime", Description: "Time duration to string => hh:mm" },
                    { Name: "TimeGapSmall", Description: "Hour, Minute, Second to time duration" },
                    { Name: "TimeGapBig", Description: "Year, Month, Day to time duration" },
                    { Name: "OpenURL", Description: "Open URL" },
                ]
            }
        },
        searchBarProp(){
            if(this.lan === "TW"){
                return [
                    {: "value", : "String", : ""},
                    {: "usebutton", : "Boolean", : ", "},
                ];
            }else{
                return [
                    {Name: "value", Type: "String", Description: "Search keyword"},
                    {Name: "usebutton", Type: "Boolean", Description: "Display button on the right and binding with update event"},
                ];
            }
        },
        searchBarEvent(){
            if(this.lan === "TW"){
                return [
                    {: "search", : "String", : ""},
                ];
            }else{
                return [
                    {Name: "search", Arugment: "String", Description: "Search by the text in arugment"},
                ];
            }
        },
        userAvatarProp(){
            if(this.lan === "TW"){
                return [
                    {: "id", : "String", : "GUID"},
                    {: "username", : "String", : ", "},
                    {: "size", : "Number", : ",  35 px"},
                ];
            }else{
                return [
                    {Name: "id", Type: "String", Description: "Account GUID"},
                    {Name: "username", Type: "String", Description: "Account name, Popup dialog display"},
                    {Name: "size", Type: "Number", Description: "Avatar size, Default 35 px"},
                ]
            }
        },
        userAvatarEvent(){
            return [];
        },
        topButtonProp(){
            if(this.lan === "TW"){
                return [
                    {: "tooltip", : "String", : ""},
                    {: "color", : "String", : ""},
                    {: "icon", : "String", : " Icon"},
                    {: "text", : "String", : ""},
                ];
            }else{
                return [
                    {Name: "tooltip", Type: "String", Description: "Tooltip text"},
                    {Name: "color", Type: "String", Description: "Button color"},
                    {Name: "icon", Type: "String", Description: "Button icon"},
                    {Name: "text", Type: "String", Description: "Button text"},
                ]
            }
        },
        topButtonEvent(){
            if(this.lan === "TW"){
                return [
                    {: "click", : "MouseEvent", : ""},
                ];
            }else{
                return [
                    {Name: "click", Arugment: "MouseEvent", Description: "Button click event"},
                ]
            }
        },
        topFilterProp(){
            if(this.lan === "TW"){
                return [
                    {: "prefix", : "String", : ""},
                    {: "label", : "String", : ""},
                    {: "value", : "String", : ""},
                    {: "items", : "Array", : ""},
                    {: "color", : "String", : ""},
                ];
            }else{
                return [
                    {Name: "prefix", Type: "String", Description: "Language search prefix"},
                    {Name: "label", Type: "String", Description: "title text"},
                    {Name: "value", Type: "String", Description: "Select object"},
                    {Name: "items", Type: "String", Description: "elements"},
                    {Name: "color", Type: "String", Description: "Component color"},
                ]
            }
        },
        topFilterEvent(){
            if(this.lan === "TW"){
                return [
                    {: "change", : "any", : ""},
                ];
            }else{
                return [
                    {Name: "change", Arugment: "any", Description: "Select item updated"},
                ]
            }
        },
        topDropdownProp(){
            if(this.lan === "TW"){
                return [
                    {: "prefix", : "String", : ""},
                    {: "text", : "String", : ""},
                    {: "items", : "Array", : ""},
                    {: "icon", : "String", : " Icon"},
                    {: "color", : "String", : ""},
                ];
            }else{
                return [
                    {Name: "prefix", Type: "String", Description: "Language search prefix"},
                    {Name: "text", Type: "String", Description: "Button text"},
                    {Name: "items", Type: "String", Description: "elements"},
                    {Name: "icon", Type: "String", Description: "Button icon"},
                    {Name: "color", Type: "String", Description: "Button color"},
                ]
            }
        },
        topDropdownEvent(){
            if(this.lan === "TW"){
                return [
                    {: "click", : "MouseEvent, Number", : ", "},
                ];
            }else{
                return [
                    {Name: "click", Arugment: "MouseEvent, Number", Description: "Select event, Return select element index"},
                ]
            }
        },
        pageSelectorProp(){
            if(this.lan === "TW"){
                return [
                    {: "page", : "Number", : ""},
                    {: "length", : "Number", : ""},
                    {: "total", : "Number", : ""},
                ];
            }else{
                return [
                    {Name: "page", Type: "Number", Description: "Current select page"},
                    {Name: "length", Type: "Number", Description: "Page length"},
                    {Name: "total", Type: "Number", Description: "Maximum page button on component"},
                ]
            }
        },
        pageSelectorEvent(){
            if(this.lan === "TW"){
                return [
                    {: "change", : "Number", : ""},
                ];
            }else{
                return [
                    {Name: "change", Arugment: "Number", Description: "Select page update"},
                ]
            }
        },
        dialogTempProp(){
            if(this.lan === "TW"){
                return [
                    {: "value", : "Boolean", : ""},
                    {: "color", : "String", : ", : 'backgroundPanel3'"},
                    {: "loading", : "Boolean", : ""},
                    {: "width", : "String", : ""},
                    {: "title", : "String", : ""},
                ];
            }else{
                return [
                    {Name: "value", Type: "Boolean", Description: "Dialog value"},
                    {Name: "color", Type: "String", Description: "Dialog background color, Default: 'backgroundPanel3'"},
                    {Name: "loading", Type: "Boolean", Description: "Loading bar display"},
                    {Name: "width", Type: "String", Description: "Dialog width"},
                    {Name: "title", Type: "String", Description: "Dialog title text"},
                ]
            }
        },
        dialogTempEvent(){
            if(this.lan === "TW"){
                return [
                    {: "change", : "MouseEvent, Boolean", : ""},
                ];
            }else{
                return [
                    {Name: "change", Arugment: "MouseEvent, Boolean", Description: "Dialog value update"},
                ]
            }
        },
        datePickerProp(){
            if(this.lan === "TW"){
                return [
                    {: "value", : "Boolean", : ""},
                    {: "min", : "Number", : ""},
                    {: "max", : "Number", : ""},
                    {: "title", : "String", : ""},
                ];
            }else{
                return [
                    {Name: "value", Type: "Boolean", Description: "Dialog value"},
                    {Name: "min", Type: "Number", Description: "Minimum date"},
                    {Name: "max", Type: "Number", Description: "Maximum date"},
                    {Name: "title", Type: "String", Description: "Dialog title text"},
                ]
            }
        },
        datePickerEvent(){
            if(this.lan === "TW"){
                return [
                    {: "change", : "MouseEvent, Boolean", : ""},
                    {: "apply", : "Number", : ""},
                ];
            }else{
                return [
                    {Name: "change", Arugment: "MouseEvent, Boolean", Description: "Dialog value update"},
                    {Name: "apply", Arugment: "Number", Description: "Date time apply"},
                ]
            }
        },
        timePickerProp(){
            if(this.lan === "TW"){
                return [
                    {: "value", : "Boolean", : ""},
                    {: "min", : "String", : ", xx:xx"},
                    {: "max", : "String", : ", xx:xx"},
                    {: "title", : "String", : ""},
                ];
            }else{
                return [
                    {Name: "value", Type: "Boolean", Description: "Dialog value"},
                    {Name: "min", Type: "String", Description: "Minimum time, xx:xx"},
                    {Name: "max", Type: "String", Description: "Maximum time, xx:xx"},
                    {Name: "title", Type: "String", Description: "Dialog title text"},
                ]
            }
        },
        timePickerEvent(){
            if(this.lan === "TW"){
                return [
                    {: "change", : "MouseEvent, Boolean", : ""},
                    {: "apply", : "Number, Number", : " (, )"},
                ];
            }else{
                return [
                    {Name: "change", Arugment: "MouseEvent, Boolean", Description: "Dialog value update"},
                    {Name: "apply", Arugment: "Number, Number", Description: "Time apply (Hour, Minute)"},
                ]
            }
        },
        timeSpanPickerProp(){

        },
        timeSpanPickerEvent(){
            
        },
    },
})
</script>