<template>
    <div v-if="lan=='TW'">
        <h2> </h2>
        <b-card id="Appearance" title="">
            <div>
                <p>: []/appearance.json</p>
                <ul> Theme
                    <li>0: </li>
                    <li>1: </li>
                </ul>
                <b-table :items="appearance"></b-table>
            </div>
        </b-card>
        <b-card id="Config" title="">
            <div>
                <p>: []/config.json</p>
                <b-table :items="config"></b-table>
            </div>
        </b-card>
    </div>
    <div v-else>
        <h2>Client Config</h2>
        <b-card id="Appearance" title="Appearance File">
            <div>
                <p>File path: [Program Root]/appearance.json</p>
                <ul> Theme
                    <li>0: White</li>
                    <li>1: Dark</li>
                </ul>
                <b-table :items="appearance"></b-table>
            </div>
        </b-card>
        <b-card id="Config" title="Config File">
            <div>
                <p>File path: [Program Root]/config.json</p>
                <b-table :items="config"></b-table>
            </div>
        </b-card>
    </div>
</template>

<script>
import vue from 'vue'
import main from '../../../../main'
export default vue.extend({
    name: "ClientDocs",
    props:{
        lan: String
    },
    computed:{
        main(){
            return main;
        },
        appearance(){
            if(this.lan == "TW"){
                return [
                    {: "Theme", : ",  0"},
                    {: "Lan", : ",  'EN'"}
                ]
            }else{
                return [
                    {Name: "Theme", Description: "Use theme, default: 0"},
                    {Name: "Lan", Description: "Use language tag, Default: 'EN'"}
                ]
            }
        },
        config(){
            if(this.lan == "TW"){
                return [
                    {: "Address", : ", , ."}
                ]
            }else{
                return [
                    {Name: "Address", Description: "Last time connection, When connection successfully created, it will store the value and try re-connect when next time open client"}
                ]
            }
        }
    },
})
</script>